// React bootstrap table next =>
// DOCS: https://react-bootstrap-table.github.io/react-bootstrap-table2/docs/
// STORYBOOK: https://react-bootstrap-table.github.io/react-bootstrap-table2/storybook/index.html
import React, { useEffect, useMemo } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory, {
  PaginationProvider,
} from "react-bootstrap-table2-paginator";
import { shallowEqual, useDispatch, useSelector } from "react-redux";
import * as actions from "../../../_redux/customers/customersActions";
import {
  getSelectRow,
  getHandlerTableChange,
  NoRecordsFoundMessage,
  PleaseWaitMessage,
  sortCaret,
  headerSortingClasses,
} from "../../../../../../_metronic/_helpers";
import * as uiHelpers from "../CustomersUIHelpers";
import * as columnFormatters from "./column-formatters";
import { Pagination } from "../../../../../../_metronic/_partials/controls";
import { useCustomersUIContext } from "../CustomersUIContext";

export function CustomersTable() {
  // Customers UI Context
  const customersUIContext = useCustomersUIContext();
  const customersUIProps = useMemo(() => {
    return {
      ids: customersUIContext.ids,
      setIds: customersUIContext.setIds,
      queryParams: customersUIContext.queryParams,
      setQueryParams: customersUIContext.setQueryParams,
      openEditCustomerPage: customersUIContext.openEditCustomerPage,
      openDetailCustomerPage: customersUIContext.openDetailCustomerPage,
      openDeleteCustomerDialog: customersUIContext.openDeleteCustomerDialog,
      openUpdateCustomerStatusDialog:customersUIContext.openUpdateCustomerStatusDialog,
      openUpdateCustomersStatusDialog:customersUIContext.openUpdateCustomersStatusDialog,

    };
  }, [customersUIContext]);

  // Getting curret state of customers list from store (Redux)
  const { currentState,currentUser } = useSelector(
    (state) => ({
       currentState: state.customers,
       currentUser: state.auth.user
    }),
    shallowEqual
  );
  const { totalCount, entities, listLoading } = currentState;

  // Customers Redux state
  const dispatch = useDispatch();
  useEffect(() => {
    // clear selections list
    customersUIProps.setIds([]);
    // server call by queryParams

    customersUIProps.queryParams.filter.dealerId=currentUser.dealerId.toString();
    customersUIProps.queryParams.filter.salesPersonId=currentUser.id.toString();
    dispatch(actions.fetchCustomers(customersUIProps.queryParams));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [customersUIProps.queryParams, dispatch]);
  // Table columns
 

  const columns = [
    {
      dataField: "id",
      text: "Id",
      sort: true,
      sortCaret: sortCaret,
      headerSortingClasses,
    },
    {
      dataField: "customerTypeId",
      text: "Mteri Tipi",
      sort: true,
      sortCaret: sortCaret,
      formatter: columnFormatters.TypeColumnFormatter,

    },
    {
      dataField: "firstname",
      text: "Mteri Ad",
      sort: true,
      sortCaret: sortCaret,
      formatter: columnFormatters.NameConcatFormatter,
    },
    {
      dataField: "identityNo",
      text: "Kimlik/Vergi No",
      sort: true,
      sortCaret: sortCaret,
      headerSortingClasses,
    },
 
    {
      dataField: "advancePayment",
      text: "Peinat",
      sort: true,
      sortCaret: sortCaret,
      formatter: columnFormatters.PriceColumnFormatter,
      formatExtraData: {
        key:"advancePayment"
      },

    },
   
    {
      dataField: "paymentMethodId",
      text: "deme Tipi",
      sort: true,
      sortCaret: sortCaret,
      formatter: columnFormatters.PaymentMethodColumnFormatter,
    },
    {
      dataField: "customerSourceId",
      text: "Alm Tr",
      sort: true,
      sortCaret: sortCaret,
      formatter: columnFormatters.CustomerSourceColumnFormatter,
    },
    {
      dataField: "phone1",
      text: "Kiisel Telefonu",
      sort: true,
      sortCaret: sortCaret,
      headerSortingClasses,
    },
    {
      dataField: "phone2",
      text: " Telefonu",
      sort: true,
      sortCaret: sortCaret,
      headerSortingClasses,
    },
    {
      dataField: "email",
      text: "E-Posta",
      sort: true,
      sortCaret: sortCaret,
      headerSortingClasses,
    },
    {
      dataField: "isActive",
      text:"Durum",
      sort: true,
      sortCaret: sortCaret,
     formatter: columnFormatters.StatusColumnFormatter,
    },
    
    {
      dataField: "action",
      text: "lemler",
      formatter: columnFormatters.ActionsColumnFormatter,
      formatExtraData: {
        openUpdateStatusDialog: customersUIProps.openUpdateCustomerStatusDialog,
        openEditPage: customersUIProps.openEditCustomerPage,
        openDetailPage: customersUIProps.openDetailCustomerPage,
        openDeleteDialog: customersUIProps.openDeleteCustomerDialog,
      },
      classes: "text-right p-0",
      headerClasses: "text-right pr-3",
      style: {
        minWidth: "100px",
      },
    },
  ];
  // Table pagination properties
  const paginationOptions = {
    custom: true,
    totalSize: totalCount,
    sizePerPageList: uiHelpers.sizePerPageList,
    sizePerPage: customersUIProps.queryParams.pageSize,
    page: customersUIProps.queryParams.pageNumber,
  };
  return (
    <>
      <PaginationProvider pagination={paginationFactory(paginationOptions)}>
        {({ paginationProps, paginationTableProps }) => {
          return (
            <Pagination
              isLoading={listLoading}
              paginationProps={paginationProps}
            >
              <BootstrapTable
                wrapperClasses="table-responsive"
                bordered={false}
                classes="table table-head-custom table-vertical-center overflow-hidden"
                bootstrap4
                remote
                keyField="id"
                data={entities === null ? [] : entities}
                columns={columns}
                defaultSorted={uiHelpers.defaultSorted}
                onTableChange={getHandlerTableChange(
                  customersUIProps.setQueryParams
                )}
                selectRow={getSelectRow({
                  entities,
                  ids: customersUIProps.ids,
                  setIds: customersUIProps.setIds,
                })}
                {...paginationTableProps}
              >
                <PleaseWaitMessage entities={entities} />
                <NoRecordsFoundMessage entities={entities} />
              </BootstrapTable>
            </Pagination>
          );
        }}
      </PaginationProvider>
    </>
  );
}
