import React from "react";
import { Formik } from "formik";

import SVG from "react-inlinesvg";
import { toAbsoluteUrl } from "../../../../_helpers/AssetsHelpers";

export class Wizard extends React.Component {
    static Page = ({ children, parentState }) => children(parentState)

    constructor(props) {
        super(props);
        this.state = {
            page: 0,
            values: props.initialValues,
        };
        this.baseState = this.state 
    }

    next = values => this.setState(state => ({
        page: Math.min(state.page + 1, this.props.children.length - 1),
        values
    }));

    previous = () =>
        this.setState(state => ({
            page: Math.max(state.page - 1, 0)
        }));

    validate = values => {
        const activePage = React.Children.toArray(this.props.children)[this.state.page];
        return activePage.props.validate ? activePage.props.validate(values) : {};
    };

    handleSubmit = (values, bag) => {
        
        const { children, onSubmit } = this.props;
        const { page } = this.state;
        const isLastPage = page === React.Children.count(children) - 1;

        if (isLastPage) {
            return onSubmit(values, bag);
        } else {
            bag.setTouched({});
            bag.setSubmitting(false);
            this.next(values);
        }
    };
    handleReset = () => {   
        this.setState(this.baseState)
    };

    render() {
        const { children, arrayProgress, schemaArray,btnSave,btnPrevious,btnNext,btnReset } = this.props;
        const { page, values } = this.state;
        const activePage = React.Children.toArray(children)[page];
        //console.log(activePage, "activePage");
        const isLastPage = page === React.Children.count(children) - 1;
        return (
            <Formik
                initialValues={values}
                enableReinitialize={false}
                validationSchema={schemaArray[page]}
                onSubmit={this.handleSubmit}
                onReset={this.handleReset}
            >
                {props => {
                    const { handleSubmit, handleReset, isSubmitting } = props;
                    return (
                        <form onSubmit={handleSubmit} onReset={handleReset} id="wizardForm">
                            <div className="wizard wizard-2">
                                <div className="wizard-nav border-right py-8 px-8 py-lg-20 px-lg-10">
                                    <div className="wizard-steps" >
                                        {arrayProgress.map((item, index) =>
                                                <div key={item.id} className="wizard-step" data-wizard-state={page === index ? "current" : "pending"}>
                                                    <div className="wizard-wrapper">
                                                        <div className="wizard-icon">
                                                            {
                                                                item.iconType === "svg" ?
                                                                    <span className="svg-icon svg-icon-2x">
                                                                        <SVG src={toAbsoluteUrl(item.icon)} />
                                                                    </span>  : <i className={item.icon}></i>
                                                            }

                                                        </div>
                                                        <div className="wizard-label">
                                                            <h3 className="wizard-title">{item.title}</h3>
                                                            <div className="wizard-desc">{item.description}</div>
                                                        </div>
                                                    </div>
                                                </div>
                                        )
                                        }
                                    </div>
                                </div>

                                <div className="wizard-body">
                                    <div className="row">
                                        <div className="pl-20 pr-10 col-md-12">
                                            {React.cloneElement(activePage, { parentState: { ...props } })}

                                            {/*begin: Wizard Actions*/}
                                            <div className="d-none justify-content-between border-top mt-5 pt-10">
                                                <div className="mr-2">
                                                    {page > 0 && <button ref={btnPrevious} type="button" onClick={this.previous} data-page={page}  className="btn btn-light-primary font-weight-bolder text-uppercase px-9 py-4" >Geri</button>}
                                                </div>
                                                <div>
                                                    <button ref={btnReset} type="reset" onClick={this.handleReset} className="btn btn-light font-weight-bolder text-uppercase px-9 py-4" disabled={isSubmitting}>Reset</button>
                                                    <button ref={isLastPage?btnSave:btnNext} data-page={page} data-pagecount={this.props.children.length} type="submit" /*(validation kullanmak istemezsen bunu yorum satrndan kar) onClick={this.next}*/ className="btn btn-primary font-weight-bolder text-uppercase px-9 py-4">leri</button> 
                                                   
                                                    {/* {isLastPage && <button ref={btnNext} type="submit" className="btn btn-success font-weight-bolder text-uppercase px-9 py-4" disabled={isSubmitting}>Tamamla</button>}
                                                    {!isLastPage && <button ref={btnNext} data-page={page} data-isLastPage={isLastPage} type="submit" className="btn btn-primary font-weight-bolder text-uppercase px-9 py-4">leri</button>} */}
                                                </div>
                                            </div>
                                            {/*end: Wizard Actions*/}                                            
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </form>
                    );
                }}
            </Formik>
        );
    }
}
