import React, { useMemo } from "react";
import { Formik } from "formik";
import { isEqual } from "lodash";
import { useStocksUIContext } from "./StocksUIContext";
import { StocksGrouping } from "./StocksGrouping";
import { CarColors,FuelTypes,GearTypes,CaseTypes,TramerTypes,FromWhoTitles,PurchaseTypes} from "./StocksUIHelper";

const prepareFilter = (queryParams, values) => {
  const { isActive,colorId,fuelTypeId,gearTypeId,caseTypeId, tramerTypeId,fromWhoId,purchaseTypeId,searchText } = values;
  const newQueryParams = { ...queryParams };
  const filter = {
    category:"",
    brand:"",
    model:"",
    modelType:"",
    colorId:"",
    fuelTypeId:"",
    gearTypeId:"",
    caseTypeId:"",
    tramerTypeId:"",
    fromWhoId:"",
    purchaseTypeId:"",
    isActive:""
  };
  filter.colorId= colorId;
  filter.fuelTypeId= fuelTypeId;
  filter.gearTypeId= gearTypeId;
  filter.caseTypeId= caseTypeId;
  filter.tramerTypeId= tramerTypeId;
  filter.fromWhoId= fromWhoId;
  filter.purchaseTypeId= purchaseTypeId;
  if (searchText) {
    filter.category= searchText;
    filter.brand= searchText;
    filter.model= searchText;
    filter.modelType= searchText;
    filter.modelYear= searchText;
    filter.plateNo= searchText;
    filter.sellingPrice= searchText;
  }
  filter.isActive=isActive;
  newQueryParams.filter = filter;
  return newQueryParams;
};

export function StocksFilter() {
  // Stocks UI Context
  const stocksUIContext = useStocksUIContext();
  const stocksUIProps = useMemo(() => {
    return {
      setQueryParams: stocksUIContext.setQueryParams,
      openNewStockDialog: stocksUIContext.openNewStockDialog,
      queryParams: stocksUIContext.queryParams,
      ids: stocksUIContext.ids
    };
  }, [stocksUIContext]);

  const applyFilter = (values) => {
    const newQueryParams = prepareFilter(stocksUIProps.queryParams, values);
    if (!isEqual(newQueryParams, stocksUIProps.queryParams)) {
      newQueryParams.pageNumber = 1;
      stocksUIProps.setQueryParams(newQueryParams);
    }
  };

  return (
    <>
      <div className="form-filtration">
        <div className="row align-items-center">
          <div className="col-md-3 margin-bottom-10-mobile">{stocksUIProps.ids.length > 0 && <StocksGrouping />} </div>
          <div className="col-md-9 margin-bottom-10-mobile">

            <Formik
              initialValues={{
                isActive: stocksUIContext.queryParams.filter.isActive, // values => All=""/Active=0/passive=1
                colorId:"",
                fuelTypeId:"",
                gearTypeId:"",
                caseTypeId:"",
                tramerTypeId:"",
                fromWhoId:"",
                purchaseTypeId:"",
                searchText: "",

              }}
              onSubmit={(values) => {
                applyFilter(values);
              }}
            >
              {({
                values,
                handleSubmit,
                handleBlur,
                handleChange,
                setFieldValue,
              }) => (
                <form onSubmit={handleSubmit} className="form form-label-right">
                 <div className="form-group d-flex justify-content-end">
              <div className="p-2">
              <select
                  className="form-control"
                  name="fuelTypeId"
                  placeholder="Yakt Tipine gre"
                  // TODO: Change this code
                  onChange={(e) => {
                    setFieldValue("fuelTypeId", e.target.value);
                    handleSubmit();
                  }}
                  onBlur={handleBlur}
                  value={values.fuelTypeId}
                >
                <option value="">Hepsi</option>
                {
                  FuelTypes.map(q=><option value={q.id}>{q.name}</option>)
                }
                </select>
                <small className="form-text text-muted">
               <b>Yakt Tipi</b>ne gre
                </small>
              </div>
              <div className="p-2">
              <select
                  className="form-control"
                  name="gearTypeId"
                  placeholder="Vites Tipine gre"
                  // TODO: Change this code
                  onChange={(e) => {
                    setFieldValue("gearTypeId", e.target.value);
                    handleSubmit();
                  }}
                  onBlur={handleBlur}
                  value={values.gearTypeId}
                >
                <option value="">Hepsi</option>
                {
                  GearTypes.map(q=><option value={q.id}>{q.name}</option>)
                }
                </select>
                <small className="form-text text-muted">
               <b>Vites Tipi</b>ne gre
                </small>
              </div>
              <div className="p-2">
              <select
                  className="form-control"
                  name="caseTypeId"
                  placeholder="Kasa Tipine gre"
                  // TODO: Change this code
                  onChange={(e) => {
                    setFieldValue("caseTypeId", e.target.value);
                    handleSubmit();
                  }}
                  onBlur={handleBlur}
                  value={values.caseTypeId}
                >
                <option value="">Hepsi</option>
                {
                  CaseTypes.map(q=><option value={q.id}>{q.name}</option>)
                }
                </select>
                <small className="form-text text-muted">
               <b>Kasa Tipi</b>ne gre
                </small>
              </div>
              <div className="p-2">
              <select
                  className="form-control"
                  name="colorId"
                  placeholder="Renge gre"
                  // TODO: Change this code
                  onChange={(e) => {
                    setFieldValue("colorId", e.target.value);
                    handleSubmit();
                  }}
                  onBlur={handleBlur}
                  value={values.colorId}
                >
                <option value="">Hepsi</option>
                {
                  CarColors.map(q=><option value={q.id}>{q.name}</option>)
                }
                </select>
                <small className="form-text text-muted">
               <b>Renge</b> gre
                </small>
              </div>
              <div className="p-2">
              <select
                  className="form-control"
                  name="fromWhoId"
                  placeholder="Kimden alndna gre"
                  // TODO: Change this code
                  onChange={(e) => {
                    setFieldValue("fromWhoId", e.target.value);
                    handleSubmit();
                  }}
                  onBlur={handleBlur}
                  value={values.fromWhoId}
                >
                <option value="">Hepsi</option>
                {
                  FromWhoTitles.map(q=><option value={q.id}>{q.name}</option>)
                }
                </select>
                <small className="form-text text-muted">
               <b>Kimden</b>e gre
                </small>
              </div>
              <div className="p-2">
              <select
                  className="form-control"
                  name="purchaseTypeId"
                  placeholder="Alm Trne gre"
                  // TODO: Change this code
                  onChange={(e) => {
                    setFieldValue("purchaseTypeId", e.target.value);
                    handleSubmit();
                  }}
                  onBlur={handleBlur}
                  value={values.purchaseTypeId}
                >
                <option value="">Hepsi</option>
                {
                  PurchaseTypes.map(q=><option value={q.id}>{q.name}</option>)
                }
                </select>
                <small className="form-text text-muted">
               <b>ALm Tr</b>ne gre
                </small>
              </div>
       
              <div className="p-2">
                <select
                  className="form-control"
                  name="isActive"
                  placeholder="Duruma gre filtrele"
                  onChange={(e) => {
                    setFieldValue("isActive", e.target.value);
                    handleSubmit();
                  }}
                  onBlur={handleBlur}
                  value={values.isActive}
                >
                  <option value="">Tm</option>
                  <option value="true">Aktif</option>
                  <option value="false">Pasif</option>
                </select>
                <small className="form-text text-muted">
                <b>Durum</b>a gre
                </small>
              </div>
           
              <div className="p-2">
                <input
                  type="text"
                  className="form-control"
                  name="searchText"
                  placeholder="Ara.."
                  onBlur={handleBlur}
                  value={values.searchText}
                  onChange={(e) => {
                    setFieldValue("searchText", e.target.value);
                    handleSubmit();
                  }}
                />
                <small className="form-text text-muted">
                 Tm kolonlarda ara...
                </small>
              </div>
            </div>
                </form>
              )}
            </Formik>
          </div>
        
        </div>
      </div>
    </>
  );
}
